export function getFakeKeyboardEvent(keyCode: number, eventName: string = 'keydown', shiftKey: boolean = false): KeyboardEvent {
  const keyboardEvent = new KeyboardEvent(eventName);

  Object.defineProperty(keyboardEvent, 'keyCode', {'value': keyCode});
  Object.defineProperty(keyboardEvent, 'shiftKey', {'value': shiftKey});

  return keyboardEvent;
}

export function jestCleanJSDOM(): void {
  document.getElementsByTagName('html')[0].innerHTML = '';
}
